<template>
  <div class="template" id="app">
    <div class="template__cover-page">
      <img
        class="template__cover-page--flower-1"
        src="./assets/flower1.png"
        alt=""
      />
      <img
        class="template__cover-page--flower-2"
        src="./assets/flower2.png"
        alt=""
      />

      <h3 class="template__user">{{user}}</h3>
      <span class="template__relationship">{{relationship}}</span>

      <p class="template__message">{{message}}</p>
    
    </div>
  </div>
</template>

<script>
export default {
  name: "App",

  data: () => ({
    user: null,
    relationship: null,
    message: null
  }),

  mounted() {
    this.getBook();
  },

  methods: {
    async getBook() {
      try {
        // read params
        const params = new Proxy(new URLSearchParams(window.location.search), {
          get: (searchParams, prop) => searchParams.get(prop),
        });

        this.relationship = params.relationship
        this.user = params.user
        this.message = params.message
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>

